html, body {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	background: black url(https://wallpapercave.com/wp/a1tlfX1.jpg);
	background-size: cover;
	overflow: hidden;
	font: Verdana;
}

ul, li {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

#root {
	width: 100%;
	height: 100%;
	background: hsla(0, 100%, 0%, 0.8s);

	.container {
		display: flex;
		flex-direction: column;
		box-sizing: border-box;
		padding: 40px;
		width: 100%;
		height: 100%;

		.loading {
			display: flex;
			align-items: center;
			justify-content: center;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 2;
			background: hsla(0, 0%, 0%, 0.8);
			font-size: 28px;
			color: white;
		}

		.back-button {
			margin-bottom: 40px;
			padding: 0 6%;
			height: 60px;
			font-size: 28px;
			color: hsla(216, 100%, 80%, 1);
			text-decoration: underline;
			cursor: pointer;

			&:hover {
				color: hsla(216, 100%, 70%, 1);
			}
		}

		.input {
			margin-bottom: 40px;
			padding: 0 6%;
			height: 60px;

			input {
				padding: 0 2px;
				width: 100%;
				height: 60px;
				border: none;
				background: transparent;
				outline: none;
				border-bottom: 2px solid white;
				color: white;
				font-size: 28px;

				&::placeholder {
					font-size: 28px;
					color: hsla(0, 100%, 100%, 0.6s);
				}
			}
		}

		.message {
			margin-bottom: 20px;
			color: white;
			font-size: 40px;

			strong {
				display: inline-block;
				transform: scale(1.25);
				line-height: 12px;
				transform-origin: left bottom;
			}
		}

		> ul {
			display: flex;
			flex: 1;

			> li {
				display: flex;
				flex-direction: column;
				flex: 1;
				margin: 0 1%;
				height: 100%;
				background: hsla(27, 100%, 42%, 0.8);
				color: white;
				cursor: pointer;

				&:first-child {
					margin-left: 0px;
				}
				&:last-child {
					margin-right: 0px;
				}

				.header {
					box-sizing: border-box;
					padding: 0 4px;
					width: 100%;
					height: 60px;
					line-height: 60px;
					background: hsla(0, 100%, 0%, 0.6s);
					font-size: 18px;
					text-align: center;
					text-transform: uppercase;
				}

				.content {
					display: flex;
					align-items: center;
					justify-content: center;
					flex: 1;
					font-size: 72px;
					text-align: center;
				}

				.details {
					display: flex;
					flex: 1;
					width: 100%;
					flex-direction: column;
					align-items: center;
					justify-content: center;

					li {
						display: flex;
						align-items: center;
						justify-content: center;
						flex: 1;
						width: 100%;
						box-shadow: 0px 0px 2px black;

						span {
							flex: 1;
							text-align: center;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 800px) {

	#root {
		.container {
			.message {
				font-size: 20px;
			}
			> ul {
				flex-wrap: wrap;
				justify-content: center;

				> li {
					margin: 0;
					border-right: 1px solid black;
					height: auto;

					&:only-child {
						height: 100%;
					}

					.content {
						font-size: 30px;
					}
				}
			}
		}
	}
}

@media (max-width: 400px) {

	#root {
		.container {
			> ul {
				> li {
					.details {
						font-size: 14px;
					}
				}
			}
		}
	}
}
